import { Component, Input, OnInit } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ILabelFormControlOptions } from './label-form-control-options.interface';

@Component({
  selector: 'rx-label-form-control',
  templateUrl: './label-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: LabelFormControlComponent,
      multi: true
    }
  ]
})
export class LabelFormControlComponent extends ValueAccessor<boolean> implements IFormControlComponent, OnInit {
  @Input()
  options: ILabelFormControlOptions;

  label: string;

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnInit() {
    this.label = this.options.labelKey ? this.translateService.instant(this.options.labelKey) : this.options.label;
  }
}
